<template>
  <div class="card">
    <div class="card-header">
      Child
    </div>
    <div class="card-body">
      <p>data : {{data}}</p>
    </div>
  </div>
</template>

<script setup>
//defineExpose : 외부에서 노출을 시키겠다!
import {ref, defineExpose} from "vue";

//외부에서 함부로 접근하지 못하도록 막음!(기본적으로 외부에서의 접근을 막아서 자신의 메소드와 데이터를 보호함)
const data = ref(10);

function method(){
  data.value -= 1;
}

//아래 코드를 사용하면 외부의 접근을 막지 않겠다! 근데 이건 쓰지 않는게 좋음...별로야!!
defineExpose({
  data,
  method
});

</script>

