<template>
  <div class="card">
    <div class="card-header">
      SetupHook
    </div>
    <div class="card-body">
      <button @click="handleBtn" class="btn btn-info btn-sm mr-2">버튼</button>
      <button @click="emitEvent" class="btn btn-info btn-sm">이벤트 발생</button>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";

export default{
  //props선언 -> 이렇게 하면 defineProps를 사용하지 않아도 된다!
  props: ["prop1"],

  //이벤트 선언
  emits: ["component-event"],

  //setup() 훅 재정의
  setup(props, context) {
    console.log("created 상태가 될 때 setup()함수 실행");

    //props 선언
    console.log(props);

    //반응형 속성 선언
    const count = ref(0);

    //이벤트 핸들러 선언
    function handleBtn(){
      console.log("handleBtn() 실행");
    }

    function emitEvent(){
      context.emit("component-event", "value1");
    }
    
    return {
      count,
      handleBtn,
      emitEvent
    }
  },
}

</script>

